:root {
  --brand-color-main: #ffe550;
  --brand-color-additional: #bbf330;
  --background-color-usual: #fefefe;
  --text-color-main: #2c2c2c;
  --button-color-main: #ffe450;
  --button-color-additional: #bbf330;
}

h1, h2, h3, h4, p, ul, ol {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  background-color: var(--background-color-usual);
  margin: 0;
  padding-top: 72px;
  font-family: Roboto, sans-serif;
}

a, .nomaker-ul ul {
  text-decoration: none;
  list-style: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.button {
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button--order-color {
  background-color: var(--brand-color-additional);
  color: var(--text-color-main);
  border: none;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
}

.button--order-color:hover, .button--order-color:focus {
  cursor: pointer;
  background-color: var(--button-color-main);
}

.button--order-color:enabled ~ .section-header {
  opacity: 0;
}

.button--order-light {
  color: var(--text-color-main);
  background-color: #fff;
  border: 2px solid #7eb101;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
}

.button--order-light:hover, .button--order-light:focus {
  cursor: pointer;
  background-color: var(--brand-color-additional);
  border: 2px solid #bbf330;
}

.button--subscribe-color {
  background-color: var(--brand-color-additional);
  color: var(--text-color-main);
  border: none;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
}

.button--subscribe-color:hover, .button--subscribe-color:focus {
  cursor: pointer;
  background-color: var(--button-color-main);
}

.button--subscribe--light {
  color: var(--text-color-main);
  background-color: #fff;
  border: 2px solid #7eb101;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.5;
}

